<template>
    <div class="wxc-item">
        <image :src="image"
               class="item-image"
               resize="cover"></image>

        <div class="category-tag" v-if="categoryTag">
            <text class="tag-text">{{categoryTag}}</text>
        </div>

        <div class="item-content">
            <text class="item-text" v-if="isTitleString">{{title}}</text>
            <wxc-special-rich-text :config-list="title" v-else></wxc-special-rich-text>

            <wxc-rich-text :config-list="desc" class="one-desc"></wxc-rich-text>

            <wxc-rich-text :config-list="tags"></wxc-rich-text>

            <div class="item-price">
                <wxc-rich-text :config-list="priceDesc"></wxc-rich-text>
                <!--<div class="price-num">-->
                <!--<text class="yen">{{yenSymbol}}</text>-->
                <!--<text class="price">{{price}}</text>-->
                <!--<text class="postfix" v-if="postfix">起</text>-->
                <!--</div>-->
            </div>
        </div>
    </div>
</template>

<style scoped>
    .wxc-item {
        width: 750px;
        height: 246px;
        flex-direction: row;
        position: relative;
    }
    .item-image {
        width: 198px;
        height: 198px;
        margin: 24px;
    }
    .item-content {
        flex: 1;
        border-bottom-width: 1px;
        border-bottom-color: #e0e0e0;
        border-bottom-style: solid;
        flex-direction: column;
        justify-content: space-between;
        padding-top: 24px;
        padding-right: 24px;
        padding-bottom: 18px;
        overflow: hidden;
    }
    .item-text {
        font-size: 28px;
        line-height: 40px;
        color: #333333;
        lines: 2;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .item-price {
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        height: 36px;
    }
    .price-num {
        flex-direction: row;
        align-items: center;
    }
    .yen {
        color: #FF5E00;
        font-size: 24px;
        line-height: 26px;
        margin-right: 2px;
        margin-top: 4px;
    }
    .price {
        color: #FF5E00;
        font-size: 36px;
        line-height: 40px;
    }
    .postfix {
        font-size: 24px;
        line-height: 28px;
        color: #A5A5A5;
        margin-top: 6px;
        margin-left: 2px;
    }
    .one-desc {
        flex-direction: row;
        flex-wrap: wrap;
    }
    .category-tag {
        padding-right: 16px;
        padding-left: 12px;
        height: 36px;
        justify-content: center;
        border-top-right-radius: 0;
        border-bottom-right-radius: 24px;
        background-color: #FFC900;
        position: absolute;
        top: 24px;
        left: 24px;
    }
    .tag-text {
        color: #3d3d3d;
        font-size: 22px;
        line-height: 36px;
    }
</style>

<script>
import { WxcRichText, WxcSpecialRichText, Utils } from 'weex-ui'
export default {
    components: { WxcRichText, WxcSpecialRichText },
    props: {
        title: {
            type: [String, Array],
            default: ''
        },
        imageText: {
            type: String,
            default: ''
        },
        image: {
            type: String,
            default: ''
        },
        desc: {
            type: Array,
            default: () => ([])
        },
        priceDesc: {
            type: [String, Array],
            default: ''
        },
        price: {
            type: [String, Number],
            default: 0
        },
        tags: {
            type: [Array, String],
            default: () => ([])
        },
        postfix: {
            type: Boolean,
            default: false
        },
        categoryTag: {
            type: String,
            default: ''
        }
    },
    data: () => ({
        yenSymbol: '\u00A5'
    }),
    computed: {
        isTitleString () {
            return Utils.isString(this.title)
        }
    }
}
</script>



